.sidebar {
	position: fixed;
	left: -350px;
	transition: left 0.5s ease;
	background-color: white;
	width: 316px;
	height: 100%;
	padding: 64px 70px;
}

.sidebar.open {
	left: 0;
}

.background {
	position: fixed;
	background-color: #000;
	opacity: 0.3;
	width: 100vw;
	height: 100vh;
}

.nav {
	display: flex;
	flex-direction: column;
	gap: 45px;
}

.nav a {
	font-weight: 600;
	font-size: 22px;
	color: #000;
	text-decoration: none;
}

.nav a:hover {
	color: #58acff;
}

.nav a.active {
	text-decoration: underline;
	color: #0059b2;
}
.close {
	position: absolute;
	top: 16px;
	right: 24px;
	background: none;
	border: none;
}

.close img {
	transition: transform 0.5s ease;
}
.close img:hover {
	transform: rotate(90deg);
}
